import { createSlice } from "@reduxjs/toolkit";

let user = createSlice({
  //   , useState
  name: "user",
  initialState: { name: "kim", age: 20 },
  reducers: {
    // state  
    changeName(state) {
      //   return "hyo " + state;
      state.name = "hyo " + state.name;
    },
    plusAge(state) {
      state.age += 1;
    },
  },
});

export let { changeName, plusAge } = user.actions;
export default user;
